.postBlock {
    font-family: 'Ubuntu', sans-serif;
   color:rgba(0, 0, 0, 0.466);
    width: 48%;
    margin-bottom: 20px;
    border: 1px solid black;
    max-height: 500px;
    display: grid;
    grid-template-rows: 1fr 5fr 1fr;
    border: none;


}

.postBlock_img img{
    max-width: 70px;
    border-radius: 50%
    
} 

.postBlock_img{
    background-color: #03645188;
    padding: 10px;
    display: grid;
    grid-template-areas: 
    "h a" 
    "h b";
    grid-template-columns: 3fr 1fr;
    grid-template-rows: 1fr 1fr;

}

.name{
    grid-area: a;
}

.time{
    grid-area: b;
    
}

.img{
    grid-area: h;

    
}


.postBlock_post{
    background-color: white;
    
    overflow: hidden;
    text-overflow: ellipsis;
   
    text-align: center;
    vertical-align: middle;
    padding: 10px;
    color:  black
}

.postBlock_likes{
    background-color: #03645188
}

.postBlock_likes{
    display:flex;
    justify-content: space-between;
    padding: 10px;
}
